        PrintAllBarcode: function (id, fileName) {
            var vm = this;

            //window.print();
            fileName = "download";
            vm.isLoading = true;
            var checkedValues = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (checkedValues.length > 0) {
                    checkedValues += ',';
                }
                checkedValues += $(this).val();
            });
            if (checkedValues != '') {
                const token = useloggedUserInfo().GetUserInfo.Token;
                //config.headers["Authorization"] = `Bearer ${token}`;    
                var url = `?catalogId=${vm.catalogId}&relatedinfoIds=${checkedValues}`;
                axios({
                    method: 'get',
                    url: process.env.VUE_APP_API_URL + `/DownloadAllBarcode/${url}`,
                    headers: {
                        'Authorization': `Bearer ${token}`,
                        'Content-Type': 'application/json'
                    },
                    responseType: 'blob'
                }).then(function (response) {
                    // const a = document.createElement('a');
                    // a.href = window.URL.createObjectURL(response.data);
                    // a.download = `${fileName}.pdf`;
                    // document.body.appendChild(a);
                    // a.click();
                    // a.remove();

                    let url = URL.createObjectURL(response.data);
                    //window.open(url); //opens the pdf in a new tab
                    vm.htmlsrc = url;
                    vm.isShowBarcodePrintPopup = true;
                    vm.isLoading = false;

                    //window.print(url);
                });
            }
            else {
                vm.ShowAlert(vm.$t('Please select at least one Barcode'), "success", vm.$t('Alert'));
                vm.isLoading = false;
            }
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        currentPageMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        ClosePOPuP: function () {
            var vm = this;
            vm.IscheckInfromation = false;
        },
        OpenPopup: function () {
            var vm = this;
            vm.IscheckInfromation = true;
        },
        ClosePOp: function () {
            var vm = this;
            vm.IscheckDeployProduct = false;
        },
        DeployProduct: function () {
            var vm = this;
            vm.IscheckDeployProduct = true;
        },
        AuditHistoryClick: function (item, tabname) {
            var vm = this;
            vm.TabName = tabname;
            vm.HistoryItems = item;
            vm.isHistoryModel = true;
        },
        CloseAuditpopup: function () {
            var vm = this;
            vm.isHistoryModel = false;
        },
        UpdateBarcode: function (item) {
            var vm = this;
            vm.BarcodeArray = [];
            const obj = {
                BarcodeNumber: item.BarcodeNumber,
                SerialNumber: item.SerialNumber == null ? "" : item.SerialNumber,
                UniqueName: item.UniqueName,
                AssetCatalogueId: item.AssetCatalogueId
            }
            vm.BarcodeArray.push(obj);
            vm.IsUpdateBarCode = true;
        },
        DownLoadFile: function (item) {
            var vm = this;
            vm.isLoading = true;
            var param = `filePath=${item.FilePath}`;
            // var url = `?fileId=${item.RelatedInfoId}&filePath=${item.FilePath}&fileName=${item.AttachmentName}&FileType=${item.FileType}`;
            DataService.DownloadAttachment(param)
                .then(response => {
                    if (response.data != null && response.status === 200) {
                        const byteData = response.data;
                        const fileURL = `data:${item.FileType};base64,${byteData}`;
                        var fileLink = document.createElement('a');
                        fileLink.href = fileURL;
                        fileLink.setAttribute('download', item.AttachmentName);
                        document.body.appendChild(fileLink);
                        fileLink.click();
                        vm.isLoading = false;
                    }
                    else {
                        vm.isLoading = false;
                    }
                });
        },
        ViewHistory: function (item) {
            this.$router.push({ name: 'BarcodeSearch', params: { Id: item.RelatedInfoId } });

        },
        ClosePOP: function () {
            var vm = this;
            vm.IsUpdateBarCode = false;
        },
        ShowCalender: function (value) {

            this.$router.push({ name: "Calendar", params: { rid: value.RelatedInfoId, lid: value.RelatedInfoLocationId, aid: value.AssetCatalogueId == null ? 0 : value.AssetCatalogueId } });
        },
        CloseCalendarpopup: function () {
            var vm = this;
            vm.Calenderpopup = false;
        }

    }
}
